$die-bg-clr: #fff;

.die {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 50px;
  background-color: $die-bg-clr;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  cursor: pointer;

  &--selected {
    background-color: #59e391;
  }

  &__text {
    font-size: 2rem;
  }
}
